.page {
  color: #060b35;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.product-card__image {
  margin-bottom: 40px;
  align-self: center;
  width: 160px;
  height: 134px;
  margin-inline: 3px;
}

.product-card__title {
  text-align: left;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__code {
  text-align: left;
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.stars-4 {
  justify-content: space-between;
  width: 96px;
  display: flex;
}

.stars-4__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  border-radius: .5px;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
}

.stars-4__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

.price {
  justify-content: space-between;
  height: 18px;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price__number {
  font-size: 16px;
  font-weight: 700;
}

.button-buy {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
}

.button-buy:hover {
  color: #00acdc;
  background-color: #f3f3f3;
  border: 1px solid #00acdc;
}

.buy-link {
  color: inherit;
  text-decoration: none;
}
